import React, { useState, useEffect } from "react";
import { useParams, Link } from "react-router-dom";
import CarCarousel from "./CarCarousel";
import axios from "axios";

const CarDetailPage = () => {
  const { id } = useParams();
  const [car, setCar] = useState(null);

  useEffect(() => {
    axios.get(`http://127.0.0.1:8000/api/cars/${id}/`)
      .then(response => {
        //  images  ,   
        const carData = {
          ...response.data,
          images: response.data.images
            ? response.data.images.map(img => typeof img === "string" ? { url: img } : img)
            : []
        };
        setCar(carData);
      })
      .catch(error => {
        console.error("   :", error);
      });
  }, [id]);

  if (!car) return <p style={{ padding: "20px" }}>    </p>;

  return (
    <div style={{ padding: "20px" }}>
      <Link to="/">   </Link>
      <h2>{car.brand} {car.model}</h2>
      <div style={{ display: "flex", gap: "20px", flexWrap: "wrap" }}>
        <CarCarousel images={car.images} carId={car.id} />
        <div style={{ maxWidth: "400px" }}>
          <p><strong> :</strong> {car.year}</p>
          <p><strong>:</strong> {car.mileage}</p>
          <p><strong>:</strong> {car.bodyType}</p>
          <p><strong>:</strong> {car.color}</p>
          <p><strong>:</strong> {car.engine}</p>
          <p><strong>:</strong> {car.price} USD</p>
          <p><strong>:</strong> {car.description}</p>
          <p><strong> :</strong> {car.comment}</p>
        </div>
      </div>
    </div>
  );
};

export default CarDetailPage;
